function numeral_systems_entropy_analysis(n, log_f) {
  for (let base = 2; base <= 36; base++) {
    let ns_string = n.toString(base);
    let count_dict = {};
    for (let i = 0; i < ns_string.length; i++) {
      count_dict[ns_string[i]] = (count_dict[ns_string[i]] || 0) + 1;
    }
    let status_str = `Base ${base}: `;
    let important = false;
    for (const [letter, count] of Object.entries(count_dict).sort((a, b) => - a[1] + b[1])) {
      const status = count / ns_string.length;
      if (status > 1 / base + 0.2) {
        important = true;
      }
      status_str += `${letter}-${(status * 100).toFixed(2)}%; `;
      if (status_str.length > 30) {
        break;
      }
    }
    if (important) {
      status_str = '!!! ' + status_str;
    }
    log_f(status_str);
  }
}

export { numeral_systems_entropy_analysis };